<template>
  <div>
    <el-collapse @change="handleChange" accordion>
      <el-collapse-item title="Css不常用伪类选择器" name="1">
        <p>:empty：选择没有子元素的目标元素</p>
        <p>:enabled：选择所有已启用的目标元素</p>
        <p>:disabled：选择所有已禁用的目标元素</p>
        <p>:checked：选择所有已选中的目标元素</p>
        <p>:not(selector)：选择每个不是selector元素的元素</p>
        <p>::selection：匹配元素中被用户选中或处于高亮状态的部分</p>
        <p>:out-of-range：匹配值在指定区间之外的input元素</p>
        <p>:in-range：匹配值在指定区间内的input元素</p>
        <p>:read-write：匹配可读及可写的元素</p>
        <p>:read-only：匹配设置只读属性的元素</p>
        <p>:required：匹配必选的元素</p>
        <p>:optional：匹配可选(非必选)的输入元素</p>
        <p>:valid：匹配输入值为合法的元素</p>
        <p>:invalid：匹配输入值为非法的元素</p>
      </el-collapse-item>
      <el-collapse-item title="后代选择器" name="2">
        <p>el1>el2：选择所有父级为el1元素的el2元素</p>
        <p>el1+el2：选择所有紧接在el1元素之后的el2元素</p>
        <p>el1~el2：选择el1元素之后的所有el2元素</p>
      </el-collapse-item>
      <el-collapse-item title="属性选择器" name="3">
        <p>[attr]：选择所有带attr属性的元素</p>
        <p>[attr=value]：选择所有attr属性值为value的元素</p>
        <p>[attr~=value]：选择所有attr属性值中包含value的元素</p>
        <p>
          [attr|=value]：选择所有attr属性值以value开头的元素(后续字符要以-连接)
        </p>
        <p>[attr^=value]：选择所有attr属性值以value开头的元素</p>
        <p>[attr$=value]：选择所有attr属性值以value结尾的元素</p>
        <p>[attr*=value]：选择所有attr属性值包含子字符串value的元素</p>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>
<script>
export default {
  methods: {
    handleChange() {},
  }
};
</script>
<style lang="less" scoped>
</style>