<template>
  <div>
    <el-upload
      class="upload-demo"
      :show-file-list="false"
      action=""
      :before-upload="uploadVideo"
      accept="video/mp4,video/mkv"
    >
      <el-button size="small" type="primary" style="margin-bottom: 20px"
        >点击上传</el-button
      >
    </el-upload>
    <video autoplay controls style="width: 50%; height: 100%"></video>
    <el-row :gutter="20">
      <el-col :span="6">
        <el-input
          v-model="startCutTime"
          placeholder="开始时间:HH-MM-SS"
        ></el-input>
      </el-col>
      <el-col :span="6">
        <el-input v-model="lastTime" placeholder="持续时间:HH-MM-SS"></el-input>
      </el-col>
      <el-col :span="6"
        ><el-input v-model="TEM_FILE_NAME" placeholder="请输入名称"></el-input
      ></el-col>
      <el-col :span="6">
        <el-button type="primary" @click="videoCut">剪辑</el-button>
      </el-col>
    </el-row>
    <el-upload
      class="upload-demo"
      :show-file-list="false"
      action=""
      :before-upload="mergeVideo"
      accept="video/mp4,video/mkv"
    >
      <el-button size="small" type="primary" style="margin-top: 20px"
        >上传合并</el-button
      >
    </el-upload>
  </div>
</template>
<script>
import { createFFmpeg, fetchFile } from "@ffmpeg/ffmpeg";
const ffmpeg = createFFmpeg();
export default {
  data() {
    return {
      videoName: "",
      orgFileBuffer: [],
      startCutTime: "00:00:00",
      lastTime: "00:00:10",
      TEM_FILE_NAME: "123.mp4",
    };
  },
  beforeMount() {
    if (!ffmpeg.isLoaded()) {
      ffmpeg.load().catch((err) => {
        console.log(err);
      });
    }
  },
  methods: {
    async uploadVideo(file) {
      this.videoName = file.name;
      this.orgFileBuffer = await file.arrayBuffer(); // 获取文件数据
      ffmpeg.FS(
        "writeFile",
        this.videoName,
        await fetchFile(new Blob([this.orgFileBuffer]))
      ); // 将视频数据写入内存
      let v = document.querySelector("video");
      v.src = URL.createObjectURL(new Blob([file])); // 将视频数据转为url
      return false;
    },
    async videoCut() {
      // startCutTime开始剪切的时间
      // lastTime持续时间
      // videoName要剪切的视频名
      // TEM_FILE_NAME剪切完成后保存到内存的视频名称
      console.log(this.startCutTime, this.lastTime, this.videoName);
      await ffmpeg.run(
        "-ss",
        this.startCutTime,
        "-to",
        this.lastTime,
        "-i",
        this.videoName,
        // "-vcodec",
        // "copy",
        // "-acodec",
        // "copy",
        this.TEM_FILE_NAME
      );
      let arrayBuffer = ffmpeg.FS("readFile", this.TEM_FILE_NAME).buffer; // 读取缓存
      let v = document.querySelector("video");
      v.src = URL.createObjectURL(new Blob([arrayBuffer])); // 转为Blob URL
      console.log("end");
    },
    async mergeVideo(file) {
      let name = file.name;
      this.orgFileBuffer = await file.arrayBuffer(); // 获取文件数据
      ffmpeg.FS(
        "writeFile",
        name,
        await fetchFile(new Blob([this.orgFileBuffer]))
      );
      await ffmpeg.run(
        "-i",
        this.videoName,
        '-c',
        'copy',
        '-vbsf',
        'h264_mp4toannexb',
        "i1.ts"
      );
      await ffmpeg.run(
        "-i",
        name,
        '-c',
        'copy',
        '-vbsf',
        'h264_mp4toannexb',
        "i2.ts"
      );
      await ffmpeg.run(
        "-i",
        'concat:i1.ts|i2.ts',
        "-c",
        "copy",
        "merge.mp4"
      );
      let arrayBuffer = ffmpeg.FS("readFile", 'merge.mp4').buffer; // 读取缓存
      let v = document.querySelector("video");
      v.src = URL.createObjectURL(new Blob([arrayBuffer])); // 转为Blob URL
      console.log(v.src)
    },
  },
};
</script>
