<template>
  <div>
    <el-collapse @change="handleChange" accordion>
      <el-collapse-item title="Number属性" name="1">
        <p>
          表示1与Number可表示的大于1的最小的浮点数之间的差值：Number.EPSILON
        </p>
        <p>JavaScript中最大的安全整数：Number.MAX_SAFE_INTEGER</p>
        <p>能表示的最大正数：Number.MAX_VALUE</p>
        <p>JavaScript 中最小的安全整数：Number.MIN_SAFE_INTEGER</p>
        <p>
          能表示的最小正数即最接近 0 的正数(实际上不会变成0)：Number.MIN_VALUE
        </p>
        <p>特殊的'非数字'值：Number.NAN</p>
        <p>特殊的负无穷大值，在溢出时返回该值：Number.NEGATIVE_INFINITY</p>
        <p>特殊的正无穷大值，在溢出时返回该值：Number.POSITIVE_INFINITY</p>
      </el-collapse-item>
      <el-collapse-item title="Number方法" name="2">
        <p>判断传递的值是否是NaN：Number.isNAN()</p>
        <p>判断传递的值类型及本身是否是有限数：Number.isFinite()</p>
        <p>判断传递的值类型是否是'number'，且是否是整数：Number.isInteger()</p>
        <p>判断传递的值是否为安全整数：Number.isSafeInteger()</p>
        <p>将字符串转换成浮点数：Number.parseFloat()</p>
        <p>将字符串转换成整数：Number.parseInt()</p>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>
<script>
export default {
  methods: {
    handleChange() {},
  }
};
</script>
