<template>
  <div>
    <el-collapse @change="handleChange" accordion>
      <el-collapse-item title="概述" name="1">
        <p>
          凡是属性名属于Symbol类型，就都是独一无二的，可以保证不会与其他属性名产生冲突
        </p>
        <p>
          注意，Symbol函数前不能使用new命令，否则会报错。这是因为生成的 Symbol
          是一个原始类型的值，不是对象。也就是说，由于 Symbol
          值不是对象，所以不能添加属性。基本上，它是一种类似于字符串的数据类型
        </p>
        <p>Symbol函数可以接受一个字符串作为参数，表示对 Symbol 实例的描述</p>
        <p>
          Symbol
          值不能与其他类型的值进行运算，会报错，但是可以显式转为字符串值和布尔值，不能转成数值
        </p>
      </el-collapse-item>
      <el-collapse-item title="Symbol属性" name="2">
        <p>
          ES2019 提供了一个实例属性description，直接返回 Symbol
          的描述：sym.description
        </p>
      </el-collapse-item>
      <el-collapse-item title="Symbol作为属性名" name="3">
        <p>
          由于每一个 Symbol 值都是不相等的，这意味着 Symbol
          值可以作为标识符，用于对象的属性名，就能保证不会出现同名的属性。这对于一个对象由多个模块构成的情况非常有用，能防止某一个键被不小心改写或覆盖
        </p>
        <p>
          在对象的内部，使用 Symbol 值定义属性时，Symbol 值必须放在方括号之中
        </p>
        <p>
          注意，Symbol
          值作为对象属性名时，不能用点运算符，因为点运算符后面总是字符串，所以不会读取标识名所指代的那个值
        </p>
        <p>Symbol 值作为属性名时，该属性还是公开属性，不是私有属性</p>
      </el-collapse-item>
      <el-collapse-item title="Symbol方法" name="4">
        <p>
          Symbol.for()：接受一个字符串作为参数，然后搜索以该参数作为名称的
          Symbol 值，如果有，就返回这个 Symbol
          值，没有就新建一个以该字符串为名称的 Symbol 值，并将其注册到全局
        </p>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>
<script>
export default {
  methods: {
    handleChange() {},
  },
};
</script>
<style lang="less" scoped>
</style>