<template>
  <div>
    <el-collapse @change="handleChange" accordion>
      <el-collapse-item title="Math对象属性" name="1">
        <p>欧拉常数，也是自然对数的底数，约等于2.718：Math.E</p>
        <p>2的自然对数，约等于0.693：Math.LN2</p>
        <p>10的自然对数，约等于2.303：Math.LN10</p>
        <p>以2为底的E的对数，约等于1.443：Math.LOG2E</p>
        <p>以10为底的E的对数，约等于0.434：Math.LOG10E</p>
        <p>圆周率：Math.PI</p>
        <p>二分之一的平方根，约等于0.707：Math.SQRT1_2</p>
        <p>2 的平方根：Math.SQRT2</p>
      </el-collapse-item>
      <el-collapse-item title="Math对象方法" name="2">
        <p>返回一个数的绝对值：Math.abs(x)</p>
        <p>返回一个数的立方根：Math.cbrt(x)</p>
        <p>返回大于一个数的最小整数，即一个数向上取整后的值：Math.ceil(x)</p>
        <p>返回小于一个数的最大整数，即一个数向下取整后的值：Math.floor(x)</p>
        <p>返回四舍五入后的整数：Math.round(x)</p>
        <p>返回一个数的整数部分，直接去除其小数点及之后的部分：Math.trunc(x)</p>
        <p>返回欧拉常数e的参数次方：Math.exp(x)</p>
        <p>返回一个数的y次幂：Math.pow(x,y)</p>
        <p>返回最接近一个数的单精度浮点型表示：Math.fround(x)</p>
        <p>返回其所有参数平方和的平方根：Math.hypot(x,y,...)</p>
        <p>返回一个数的自然对数：Math.log(x)</p>
        <p>返回一个数以10为底数的对数：Math.log10(x)</p>
        <p>返回一个数以2为底数的对数：Math.log2(x)</p>
        <p>返回零到多个数值中最大值：Math.max(x,y,...)</p>
        <p>返回零到多个数值中最小值：Math.min(x,y,...)</p>
        <p>返回一个0到1之间的伪随机数：Math.random()</p>
        <p>返回一个数的符号，判断是正数、负数还是0：Math.sign(x)</p>
        <p>返回一个数的平方根：Math.sqrt(x)</p>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>
