<template>
  <div>
    <el-collapse @change="handleChange" accordion>
      <el-collapse-item title="Promise概述" name="1">
        <p>
          Promise
          是异步编程的一种解决方案，比传统的解决方案——回调函数和事件——更合理和更强大。它由社区最早提出和实现，ES6
          将其写进了语言标准，统一了用法，原生提供了Promise对象，从语法上说，Promise
          是一个对象，从它可以获取异步操作的消息
        </p>
        <p>
          Promise对象有以下两个特点。
          （1）对象的状态不受外界影响。Promise对象代表一个异步操作，有三种状态：pending（进行中）、fulfilled（已成功）和rejected（已失败）。只有异步操作的结果，可以决定当前是哪一种状态，任何其他操作都无法改变这个状态。
          （2）一旦状态改变，就不会再变，任何时候都可以得到这个结果。Promise对象的状态改变，只有两种可能：从pending变为fulfilled和从pending变为rejected。只要这两种情况发生，状态就凝固了，不会再变了，会一直保持这个结果，这时就称为
          resolved（已定型）。
        </p>
        <p>
          有了Promise对象，就可以将异步操作以同步操作的流程表达出来，避免了层层嵌套的回调函数。此外，Promise对象提供统一的接口，使得控制异步操作更加容易。
        </p>
        <p>
          Promise也有一些缺点。首先，无法取消Promise，一旦新建它就会立即执行，无法中途取消。其次，如果不设置回调函数，Promise内部抛出的错误，不会反应到外部。第三，当处于pending状态时，无法得知目前进展到哪一个阶段（刚刚开始还是即将完成）。
        </p>
      </el-collapse-item>
      <el-collapse-item title="Promise方法" name="2">
        <p>
          用于将多个 Promise
          实例，包装成一个新的Promise实例：Promise.all(iterable)
        </p>
        <p>
          用于指定不管 Promise
          对象最后状态如何，都会执行的操作：p.finally(onFinally)
        </p>
        <p>用于指定promise发生错误时的回调函数：p.catch(onRejected)</p>
        <p>
          为Promise实例添加状态改变时的回调函数：p.then(onFulfilled[,
          onRejected])
        </p>
        <p>
          返回一个 promise，一旦迭代器中的某个promise解决或拒绝，返回的
          promise就会解决或拒绝：Promise.race(iterable)
        </p>
        <p>
          返回一个在所有给定的promise已被决议或被拒绝后决议的promise，并带有一个对象数组，每个对象表示对应的promise结果：Promise.allSettled(iterable)
        </p>
        <p>返回一个以给定值解析后的Promise对象：Promise.resolve(value)</p>
        <p>返回一个带有拒绝原因的Promise对象：Promise.reject(reason)</p>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>
<script>
export default {
  methods: {
    handleChange() {},
  },
};
</script>
