<template>
  <div>
    <el-collapse @change="handleChange" accordion>
      <el-collapse-item title="数组的解构赋值" name="1">
        <p>可以从数组中提取值，按照对应位置，对变量赋值</p>
        <p>
          本质上，这种写法属于“模式匹配”，只要等号两边的模式相同，左边的变量就会被赋予对应的值
        </p>
        <p>
          解构赋值允许指定默认值,ES6内部使用严格相等运算符（===），判断一个位置是否有值。所以，只有当一个数组成员严格等于undefined，默认值才会生效
        </p>
        <p>如果解构不成功，变量的值就等于undefined</p>
        <p>
          另一种情况是不完全解构，即等号左边的模式，只匹配一部分的等号右边的数组。这种情况下，解构依然可以成功
        </p>
        <p>
          解构赋值允许指定默认值，如果默认值是一个表达式，那么这个表达式是惰性求值的，即只有在用到的时候，才会求值
        </p>
      </el-collapse-item>
      <el-collapse-item title="对象的解构赋值" name="2">
        <p>
          解构不仅可以用于数组，还可以用于对象。对象的解构与数组有一个重要的不同。数组的元素是按次序排列的，变量的取值由它的位置决定；而对象的属性没有次序，变量必须与属性同名，才能取到正确的值
        </p>
        <p>对象的解构赋值，可以很方便地将现有对象的方法，赋值到某个变量</p>
        <p>
          如果变量名与属性名不一致，必须写成下面这样：let { foo: baz } = { foo:
          'aaa', bar: 'bbb' }
        </p>
        <p>
          对象的解构赋值的内部机制，是先找到同名属性，然后再赋给对应的变量。真正被赋值的是后者，而不是前者
        </p>
        <p>与数组一样，解构也可以用于嵌套结构的对象</p>
        <p>注意，对象的解构赋值可以取到继承的属性</p>
        <p>
          对象的解构也可以指定默认值，默认值生效的条件是，对象的属性值严格等于undefined
        </p>
        <p>由于数组本质是特殊的对象，因此可以对数组进行对象属性的解构</p>
      </el-collapse-item>
      <el-collapse-item title="字符串的解构赋值" name="3">
        <p>
          字符串也可以解构赋值。这是因为此时，字符串被转换成了一个类似数组的对象
        </p>
        <p>
          类似数组的对象都有一个length属性，因此还可以对这个属性解构赋值：let
          {length : len} = 'hello'
        </p>
      </el-collapse-item>
      <el-collapse-item title="数值和布尔值的解构赋值" name="4">
        <p>解构赋值时，如果等号右边是数值和布尔值，则会先转为对象</p>
        <p>
          解构赋值的规则是，只要等号右边的值不是对象或数组，就先将其转为对象。由于undefined和null无法转为对象，所以对它们进行解构赋值，都会报错
        </p>
      </el-collapse-item>
      <el-collapse-item title="函数参数的解构赋值" name="5">
        <p>函数参数的解构也可以使用默认值</p>
      </el-collapse-item>
      <el-collapse-item title="圆括号问题" name="6">
        <p>
          解构赋值虽然很方便，但是解析起来并不容易。对于编译器来说，一个式子到底是模式，还是表达式，没有办法从一开始就知道，必须解析到（或解析不到）等号才能知道。由此带来的问题是，如果模式中出现圆括号怎么处理。ES6的规则是，只要有可能导致解构的歧义，就不得使用圆括号。但是，这条规则实际上不那么容易辨别，处理起来相当麻烦。因此，建议只要有可能，就不要在模式中放置圆括号。
        </p>
        <p>
          以下三种解构赋值不得使用圆括号：1.变量声明语句；2.函数参数；3.赋值语句的模式
        </p>
        <p>
          可以使用圆括号的情况只有一种：赋值语句的非模式部分，可以使用圆括号
        </p>
      </el-collapse-item>
      <el-collapse-item title="用途" name="7">
        <p>1.交换变量的值</p>
        <p>2.从函数返回多个值</p>
        <p>3.函数参数的定义，可以方便地将一组参数与变量名对应起来</p>
        <p>4.函数参数的默认值</p>
        <p>5.提取 JSON 数据</p>
        <p>6.遍历 Map 结构</p>
        <p>7.输入模块的指定方法</p>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>
<script>
export default {
  methods: {
    handleChange() {},
  },
};
</script>
