<template>
  <div>
    <el-collapse @change="handleChange" accordion>
      <el-collapse-item title="Object方法" name="1">
        <p>
          将所有可枚举属性的值从一个或多个源对象复制到目标对象，返回此对象：Object.assign(target,
          ...sources)
        </p>
        <p>
          创建一个新对象,使用现有的对象来提供新创建的对象的__proto__：Object.create(proto[,
          propertiesObject])
        </p>
        <p>
          直接在一个对象上定义一个新属性，或者修改一个对象的现有属性，并返回此对象：Object.defineProperty(obj,
          prop, descriptor)
        </p>
        <p>
          直接在一个对象上定义新属性，或者修改对象的现有属性，并返回此对象：Object.defineProperties(obj,
          props)
        </p>
        <p>返回给定对象自身可枚举属性的[key, value]数组：Object.entries(obj)</p>
        <p>
          Object.entries()的逆操作，用于将一个键值对数组转为对象：Object.fromEntries(arr)
        </p>
        <p>返回给定对象自身可枚举值的数组：Object.values(obj)</p>
        <p>返回给定对象自身可枚举属性名称的数组:Object.keys(obj)</p>
        <p>冻结对象：其他代码不能删除或更改任何属性：Object.freeze(obj)</p>
        <p>
          封闭一个对象，使其不能添加新属性而且不能删除现有属性，但是可以修改属性值：Object.seal(obj)
        </p>
        <p>防止对象的任何扩展：Object.preventExtensions(obj)</p>
        <p>
          返回一个数组，它包含了指定对象所有的可枚举或不可枚举的属性名：Object.getOwnPropertyNames(obj)
        </p>
        <p>
          返回一个给定对象自身的所有 Symbol
          属性的数组：Object.getOwnPropertySymbols(obj)
        </p>
        <p>
          返回指定对象上一个自有属性对应的属性描述符：Object.getOwnPropertyDescriptor(obj,
          prop)
        </p>
        <p>返回指定对象的原型对象：Object.getPrototypeOf(obj)</p>
        <p>设置对象的原型：Object.setPrototypeOf(obj, prototype)</p>
        <p>判断两个值是否是相同的值：Object.is(value1, value2)</p>
        <p>判断对象是否可扩展：Object.isExtensible(obj)</p>
        <p>判断对象是否已经冻结：Object.isFrozen(obj)</p>
        <p>判断对象是否已经密封：Object.isSealed()</p>
      </el-collapse-item>
      <el-collapse-item title="Object属性名表达式" name="2">
        <p>
          JavaScript定义对象的属性，有两种方法，方法一是直接用标识符作为属性名，方法二是用表达式作为属性名，这时要将表达式放在方括号之内：obj['a'
          + 'bc'] = 123;
        </p>
        <p>表达式还可以用于定义方法名</p>
        <p>注意，属性名表达式与简洁表示法，不能同时使用，会报错</p>
        <p>
          注意，属性名表达式如果是一个对象，默认情况下会自动将对象转为字符串[object
          Object]
        </p>
      </el-collapse-item>
      <el-collapse-item title="super关键字" name="3">
        <p>
          this关键字总是指向函数所在的当前对象，ES6新增了一个类似的关键字super，指向当前对象的原型对象
        </p>
        <p>
          注意，super关键字表示原型对象时，只能用在对象的方法之中，用在其他地方都会报错，目前，只有对象方法的简写法可以让JavaScript引擎确认，定义的是对象的方法
        </p>
      </el-collapse-item>
      <el-collapse-item title="链判断运算符" name="4">
        <p>
          编程实务中，如果读取对象内部的某个属性，往往需要判断一下该对象是否存在，但是层层判断非常麻烦，因此ES2020引入了“链判断运算符”?.，简化了写法。
        </p>
        <p>
          链判断运算符有三种用法：1.obj?.prop // 对象属性；2.obj?.[expr] //
          对象属性；3.func?.(...args) // 函数或对象方法的调用。
        </p>
        <p>
          如果属性链有圆括号，链判断运算符对圆括号外部没有影响，只对圆括号内部有影响
        </p>
        <p>一般来说，使用?.运算符的场合，不应该使用圆括号</p>
      </el-collapse-item>
      <el-collapse-item title="NULL判断符" name="5">
        <p>
          ES2020 引入了一个新的 Null
          判断运算符??。它的行为类似||，但是只有运算符左侧的值为null或undefined时，才会返回右侧的值
        </p>
        <p>
          这个运算符的一个目的，就是跟链判断运算符?.配合使用，为null或undefined的值设置默认值
        </p>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>
<script>
export default {
  methods: {
    handleChange() {},
  },
};
</script>
<style lang="less" scoped>
</style>