<template>
  <div>
    <el-collapse @change="handleChange" accordion>
      <el-collapse-item title="栈方法" name="1">
        <p>删除并返回数组的最后一个元素：pop()</p>
        <p>向数组末尾添加一个或多个元素，返回新的长度：push()</p>
      </el-collapse-item>
      <el-collapse-item title="队列方法" name="2">
        <p>向数组开头删除一个或多个元素返回被删掉的元素，shift()</p>
        <p>向数组开头添加一个或多个元素，返回新的长度：unshift()</p>
      </el-collapse-item>
      <el-collapse-item title="操作方法" name="3">
        <p>连接两个或更多的数组，返回生成新数组的副本：cancat()</p>
        <p>把数组所有元素放入一个字符串，通过指定分隔符分隔：join(分隔符)</p>
        <p>从数组中返回选定的元素，不改变原数组：slice(start，end)(左闭右开)</p>
        <p>
          删除数组指定位置开始的指定数量元素，并可同时添加指定元素，改变原数组：splice(start，number，items….)
        </p>
        <p>
          将类似数组的对象和可遍历的对象转换为真正的数组：Array.from(array,
          function)
        </p>
        <p>将一组值转换为数组：Array.of(x,y,...)</p>
        <p>
          在当前数组内部，将指定位置的成员复制到其他位置（会覆盖原有成员），然后返回当前数组：copyWithin(target,
          start=0, end=this.length)
        </p>
        <p>返回使用指定值填充的数组：fill(x, start, end)</p>
        <p>返回将嵌套的数组拉平成一维数组的新数组：flat(需要往下拉平的层数)</p>
      </el-collapse-item>
      <el-collapse-item title="重排序方法" name="4">
        <p>对数组元素进行基于字符编码的排序：sort(可添加排序函数)</p>
        <p>颠倒数组元素的顺序：reverse()</p>
      </el-collapse-item>
      <el-collapse-item title="迭代方法" name="5">
        <p>检测数组中所有元素是否满足给定条件：every(条件函数)</p>
        <p>检测数组中是否有元素满足条件：some(条件函数)</p>
        <p>返回原数组中所有满足给定条件的元素组成的新数组：filter(条件函数)</p>
        <p>返回数组中满足条件的第一个元素：find(条件函数)</p>
        <p>返回数组中满足条件的第一个元素的index：findIndex(条件函数)</p>
        <p>返回经过条件处理后的新数组：map(条件函数)</p>
        <p>返回一个遍历所有键名的遍历器：keys()</p>
        <p>返回一个遍历所有键值的遍历器：values()</p>
        <p>返回一个遍历所有键值对的遍历器：entries()</p>
        <p>检测数组中是否包含给定的值：includes()</p>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>
<script>
export default {
  methods: {
    handleChange() {},
  }
};
</script>
<style lang="less" scoped>
</style>