import Vue from 'vue'
import VueRouter from 'vue-router'

import FirstPage from '../components/FirstPage.vue'
import String from '../components/基本数据类型/String.vue'
import Number from '../components/基本数据类型/Number.vue'
import Object from '../components/基本数据类型/Object.vue'
import Symbol from '../components/基本数据类型/Symbol.vue'

import Array from '../components/静态对象/Array.vue'
import Math from '../components/静态对象/Math.vue'
import RegExp from '../components/静态对象/Reg.vue'
import Dom from '../components/静态对象/Dom.vue'
import Promise from '../components/静态对象/Promise.vue'
import Set from '../components/静态对象/Set.vue'
import Map from '../components/静态对象/Map.vue'

import Strict from '../components/摘抄/Strict.vue'
import FlexBox from '../components/摘抄/FlexBox.vue'
import WebFont from '../components/摘抄/WebFont.vue'
import Weilei from '../components/摘抄/Weilei.vue'
import UncommonCss from '../components/摘抄/UncommonCSS.vue'
import Destruct from '../components/摘抄/Destruct.vue'
import TemplateString from '../components/摘抄/TemplateString.vue'
import BasicConcept from '../components/摘抄/BasicConcept.vue'
import Cmd from '../components/摘抄/CMD.vue'


import H5Css from '../components/面试题/H5andCss.vue'
import Js from '../components/面试题/Js.vue'
import VueView from '../components/面试题/Vue.vue'
import HttpView from '../components/面试题/Http.vue'
import Others from '../components/面试题/Others.vue'

import VideoEdit from '../components/工具/VideoEdit.vue'

Vue.use(VueRouter)
const router = new VueRouter({
    routes: [
        { path: '/', redirect: '/firstpage' },
        {
            path: '/firstpage',
            component: FirstPage,
            redirect: '/string',
            children: [
                //基本数据类型
                { path: '/string', component: String },
                { path: '/number', component: Number },
                { path: '/object', component: Object },
                { path: '/symbol', component: Symbol },
                //静态对象
                { path: '/array', component: Array },
                { path: '/set', component: Set },
                { path: '/map', component: Map },
                { path: '/math', component: Math },
                { path: '/reg', component: RegExp },
                { path: '/dom', component: Dom },
                { path: '/promise', component: Promise },
                //摘抄
                { path: '/strict', component: Strict },
                { path: '/flexbox', component: FlexBox },
                { path: '/webfont', component: WebFont },
                { path: '/weilei', component: Weilei },
                { path: '/uncommoncss', component: UncommonCss },
                { path: '/templatestring', component: TemplateString },
                { path: '/destruct', component: Destruct },
                { path: '/basic', component: BasicConcept },
                { path: '/cmd', component: Cmd },
                //面试题
                { path: '/h5css', component: H5Css },
                { path: '/js', component: Js },
                { path: '/vue', component: VueView },
                { path: '/http', component: HttpView },
                { path: '/others', component: Others },
                //工具
                { path: '/videoEdit', component: VideoEdit }
            ]
        }
    ]
})
export default router