<template>
  <div>
    <el-collapse @change="handleChange" accordion>
      <el-collapse-item title="正则表达式方法" name="1">
        <p>改变和重新编译正则表达式：compile()</p>
        <p>返回检索到的正则表达式内容：RegExc.exec(String)</p>
        <p>检测字符串是否包含正则表达式的内容：RegExc.test(String)</p>
        <p>
          一次性取出所有匹配，返回的是一个遍历器（Iterator），而不是数组：RegExc.matchAll(String)
        </p>
      </el-collapse-item>
      <el-collapse-item title="修饰符" name="2">
        <p>不分大小写：i</p>
        <p>查找所有匹配：g</p>
        <p>
          查找所有匹配,匹配必须从剩余的第一个位置开始：y。y修饰符的设计本意，就是让头部匹配的标志^在全局匹配中都有效
        </p>
        <p>使'.'可以匹配任意单个字符：s</p>
        <p>正确处理大于\uFFFF的 Unicode 字符：u</p>
        <p>规定正则表达式可以执行多行匹配：m</p>
      </el-collapse-item>
      <el-collapse-item title="方括号" name="3">
        <p>查找指定范围的字符：[abc]</p>
        <p>查找不在范围内的字符：[^abc]</p>
      </el-collapse-item>
      <el-collapse-item title="元字符" name="4">
        <p>除了换行和行结束符的任一字符：.</p>
        <p>单词(数字，字母，下划线)字符：\w</p>
        <p>非单词字符：\W</p>
        <p>数字字符：\d</p>
        <p>非数字字符：\D</p>
        <p>空白字符：\s</p>
        <p>非空白字符：\S</p>
        <p>匹配单词边界：\b</p>
        <p>匹配非单词边界：\B</p>
        <p>查找NUL(null)字符：\0</p>
        <p>换页符：\f</p>
        <p>回车符：\r</p>
        <p>制表符：\t</p>
        <p>垂直制表符：\v</p>
        <p>八进制规定的字符：\xxx</p>
        <p>十六进制规定的字符：\xdd</p>
        <p>
          允许正则表达式匹配符合Unicode某种属性的所有字符：\p{UnicodePropertyName=UnicodePropertyValue}
        </p>
        <p>
          允许正则表达式匹配不符合Unicode某种属性的所有字符：\P{UnicodePropertyName=UnicodePropertyValue}
        </p>
      </el-collapse-item>
      <el-collapse-item title="量词" name="5">
        <p>匹配包含至少一个n的字符串：n+</p>
        <p>匹配包含任意个n的字符串：n*</p>
        <p>匹配包含至多一个n的字符串：n?</p>
        <p>匹配包含x个n的序列的字符串：n{x}</p>
        <p>匹配包含x到y个n的序列的字符串：n{x，y}</p>
        <p>匹配包含至少x个n的序列的字符串：n{x，}</p>
        <p>匹配任何以n结尾的字符串：n$</p>
        <p>匹配任何以n开头的字符串：^n</p>
        <p>(先行断言)匹配其后紧接字符串n的字符串：RegExc(?=n)</p>
        <p>(先行断言)匹配其后没有紧接字符串n的字符串：RegExc(?!=n)</p>
        <p>(后行断言)匹配其后紧接字符串n的字符串：(?&lt;=n)RegExc</p>
        <p>(后行断言)匹配其后没有紧接字符串n的字符串：(?&lt;!=n)RegExc</p>
      </el-collapse-item>
      <el-collapse-item title="属性" name="6">
        <p>正则实例对象新增unicode属性，表示是否设置了u修饰符</p>
        <p>正则实例对象新增sticky属性，表示是否设置了y修饰符</p>
        <p>正则表达式新增了flags属性，会返回正则表达式的修饰符</p>
        <p>正则表达式新增了dotAll属性，表示是否处在dotAll模式。</p>
      </el-collapse-item>
      <el-collapse-item title="具名组匹配" name="7">
        <p>
          正则表达式使用圆括号进行组匹配，<code
            >const RE_DATE = /(\d{4})-(\d{2})-(\d{2})/;const matchObj =
            RE_DATE.exec('1999-12-31')。</code
          >组匹配的一个问题是，每一组的匹配含义不容易看出来，而且只能用数字序号（比如matchObj[1]）引用，要是组的顺序变了，引用的时候就必须修改序号。
        </p>
        <p>
          ES2018 引入了具名组匹配（Named Capture
          Groups），允许为每一个组匹配指定一个名字，<code
            >const RE_DATE =
            /(?&lt;year&gt;\d{4})-(?&lt;month&gt;\d{2})-(?&lt;day&gt;\d{2})/;const
            matchObj = RE_DATE.exec('1999-12-31')</code
          >然后就可以在exec方法返回结果的groups属性上引用该组名。同时，数字序号（matchObj[1]）依然有效，具名组匹配等于为每一组匹配加上了ID，便于描述匹配的目的。如果组的顺序变了，也不用改变匹配后的处理代码，如果要在正则表达式内部引用某个“具名组匹配”，可以使用\k&lt;组名&gt;的写法
        </p>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>
<script>
export default {
  methods: {
    handleChange() {},
  },
};
</script>
<style lang="less" scoped>
</style>