<template>
  <div>
    <el-collapse @change="handleChange" accordion>
      <el-collapse-item title="Set操作方法" name="1">
        <p>在Set对象尾部添加一个元素。返回该Set对象：mySet.add(value);</p>
        <p>
          从一个 Set
          对象中删除指定的元素，成功删除返回true，否则返回false：mySet.delete(value);
        </p>
        <p>
          返回一个布尔值来指示对应的值value是否存在Set对象中：mySet.has(value);
        </p>
        <p>用来清空一个Set对象中的所有元素：mySet.clear();</p>
      </el-collapse-item>
      <el-collapse-item title="Set遍历方法" name="2">
        <p>
          返回一个元素类似[value,value]数组形式的迭代器对象，其中value是set集合对象中的每个元素：mySet.entries();
        </p>
        <p>
          返回一个按照原set对象中元素插入顺序返回其所有元素的迭代器对象：mySet.values()/mySet.keys();
        </p>
        <p>
          根据集合中元素的插入顺序，依次执行提供的回调函数：mySet.forEach(callback[,
          thisArg]);
        </p>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>
<script>
export default {
  methods: {
    handleChange() {},
  }
};
</script>
