<template>
	<div>
		<el-collapse @change="handleChange" accordion>
			<el-collapse-item title="逻辑非" name="1">
				<p>如果操作数是一个对象，返回 false；</p>
				<p>如果操作数是一个空字符串，返回 true；</p>
				<p>如果操作数是一个非空字符串，返回 false；</p>
				<p>如果操作数是数值 0，返回 true；</p>
				<p>如果操作数是任意非 0 数值（包括 Infinity），返回 false；</p>
				<p>如果操作数是 null，返回 true；</p>
				<p>如果操作数是 NaN，返回 true；</p>
				<p>如果操作数是 undefined，返回 true；</p>
			</el-collapse-item>
			<el-collapse-item title="逻辑与" name="2">
				<p>如果第一个操作数是对象，则返回第二个操作数；</p>
				<p>
					如果第二个操作数是对象，则只有在第一个操作数的求值结果为 true
					的情况下才会返回该对象；
				</p>
				<p>如果两个操作数都是对象，则返回第二个操作数；</p>
				<p>如果有一个操作数是 null，则返回 null；</p>
				<p>如果有一个操作数是 NaN，则返回 NaN；</p>
				<p>如果有一个操作数是 undefined，则返回 undefined；</p>
			</el-collapse-item>
			<el-collapse-item title="逻辑或" name="3">
				<p>如果第一个操作数是对象，则返回第一个操作数；</p>
				<p>如果第一个操作数的求值结果为 false，则返回第二个操作数；</p>
				<p>如果两个操作数都是对象，则返回第一个操作数；</p>
				<p>如果两个操作数都是 null，则返回 null；</p>
				<p>如果两个操作数都是 NaN，则返回 NaN；</p>
				<p>如果两个操作数都是 undefined，则返回 undefined；</p>
			</el-collapse-item>
			<el-collapse-item title="排序" name="4">
				<p>冒泡排序：比较相邻的元素。如果第一个比第二个大，就交换他们两个，对每一对相邻元素做同样的工作，从开始第一对到结尾的最后一对；</p>
				<p>选择排序：依次比较，找到最小的数放到第一位，再从剩下的数中找到最小的放到第二个，直到剩下数个数为零；</p>
				<p>插入排序：类似从桌面抓牌，抽牌然后和手中的牌依次比较，插入到第一个比新抓的牌小的后面，直到无牌可抓；</p>
				<p>壳（shell）排序：按照不同间隔跳着取元素，将原序列分成几个子序列，对子序列进行插入排序，然后不断减小间隔，直到间隔为一，实际上是对插入排序的升级简化；</p>
				<p>归并排序：将序列不断二分，直到子序列长度为一，然后逐个比较合并，在算法上可以用迭代书写；</p>
				<p>快速排序：从数列取一个数作为基准数，依次和剩下的数比较，小的放左边，大的放右边，再对左右分区继续相同操作；</p>
				<p>堆排序：将序列构成最大堆，然后将根节点和末尾元素交换，重新调整使剩下元素满足最大堆，然后继续交换根节点和次末尾元素，重复直到最大堆长度为一；简单排序的升级版；</p>
				<p>拓扑排序：用来排序具有依赖关系的“任务”；</p>
				<p>锦标赛排序：序列中两两比较，取较大者进入下一轮比较，直到决出最大者，然后剩下的继续比较，依次决出剩下的名次，适合从N个选手中选出K个选手；</p>
				<p>基数排序：先比较最低位，按0~9依次从小到大第一次排序，然后比较次低位，继续从小到大排序，直到按最高位排序完成，即得到最终序列。</p>
			</el-collapse-item>
		</el-collapse>
	</div>
</template>
<script>
	export default {
		methods: {
			handleChange() {},
		},
	};
</script>
<style lang="less" scoped>
</style>
