<template>
  <div>
    <el-collapse @change="handleChange" accordion>
      <el-collapse-item title="Document对象属性/方法" name="1">
        <p>返回当前获得焦点的元素：document.activeElement</p>
        <p>
          向文档添加事件：document.addEventListener(event，function，useCapture)
        </p>
        <p>
          移除由addEventListener添加的事件：document.removeEventListener(event，function，useCapture)
        </p>
        <p>返回当前文档所有的锚点：document.anchors[]</p>
        <p>返回当前文档所有的form对象：document.forms[]</p>
        <p>返回当前文档所有的image对象：document.images[]</p>
        <p>返回当前文档所有的链接的数组：document.links[]</p>
        <p>向文档写HTML表达式或JavaScript代码：document.write()</p>
        <p>
          向文档写HTML表达式或JavaScript代码，并在每个表达式后写一个换行符：document.writeIn()
        </p>
        <p>
          打开一个输出流来收集document.write()或document.writeIn()输出的内容：document.open(MIMEtype，replace)
        </p>
        <p>
          关闭由document.open打开的输出流，并显示选定的数据：document.close()
        </p>
        <p>设置或返回与当前文档有关的所有cookie：document.cookie</p>
        <p>
          创建一个指定名称的属性并返回该属性：document.createAttribute(attributename)
        </p>
        <p>创建注释节点：document.createComment(text)</p>
        <p>
          创建并返回一个附有所有新节点的文档碎片：document.createDocumentFragment()
        </p>
        <p>创建并返回一个元素：document.createElement()</p>
        <p>创建并返回一个文本节点：document.createTextNode()</p>
        <p>
          返回所有指定类名的元素的集合：document.getElementsByClassName(classname)
        </p>
        <p>返回拥有指定id的第一个对象：document.getElementById()</p>
        <p>返回带有指定名称的所有对象的集合：document.getElementsByName()</p>
        <p>
          返回带有指定标签名的所有对象的集合：document.getElementsByTagName()
        </p>
        <p>
          返回文档中匹配指定CSS选择器的第一个元素：document.querySelector(Css
          selector)
        </p>
        <p>
          返回文档中匹配指定CSS选择器的所有元素节点列表：document.querySelectorAll(Css
          selector)
        </p>
        <p>返回处理该文档的DOMImplementation对象：document.implementation；</p>
        <p>从另一个文档复制一个节点到当前文档：document.importNode()</p>
        <p>合并相邻的文本节点并删除空的文本节点：document.normalize()</p>
      </el-collapse-item>
      <el-collapse-item title="HTML元素属性\方法" name="2">
        <p>设置或返回单选按钮快捷键：element.accessKey=accesskey</p>
        <p>设置或返回元素的类名：element.className</p>
        <p>设置或返回元素的id：element.id</p>
        <p>设置或返回元素的内容：element.innerHTML</p>
        <p>设置或返回元素的内容是否可编辑：element.contentEditable</p>
        <p>设置或返回元素的文字方向：element.dir=text.direction</p>
        <p>以字符串形式返回一个元素的标签名：element.tagName</p>
        <p>设置或返回指定元素的文本内容：element.textContent</p>
        <p>设置或返回元素的title属性值：element.title</p>
        <p>返回节点集合的数目：element.length</p>
        <p>
          向指定元素添加事件：element.addEventListener(event，function，useCapture)
        </p>
        <p>
          移除指定元素的事件：element.removeEventListener(event，function，useCapture)
        </p>
        <p>
          向指定节点的子节点的列表末尾添加新的节点：element.appendChild(node)
        </p>
        <p>返回元素所有属性的集合：element.attributes[]</p>
        <p>返回一个元素子节点的数组：element.childNodes[]</p>
        <p>返回元素的第一个子节点：element.firstChild</p>
        <p>返回元素的最后一个子节点：element.lastChild</p>
        <p>返回元素其后紧跟的元素：element.nextSibling</p>
        <p>返回元素紧接之前的元素：element.previousSibling</p>
        <p>依据元素类型返回其名称：element.nodeName</p>
        <p>依据元素类型返回节点类型，用数字表示：element.nodeType</p>
        <p>设置或返回节点的值：element.nodeValue</p>
        <p>设置或返回元素的样式属性：element.style</p>
        <p>为元素设置焦点：element.focus()</p>
        <p>返回指定元素的属性值：element.getAttribute()</p>
        <p>设置指定元素的属性值：element.setAttribute()</p>
        <p>返回指定元素的属性节点：element.getAttributeNode()</p>
        <p>设置指定元素的属性节点：element.setAttributeNode()</p>
        <p>
          返回指定标签名元素的所有子元素集合：element.getElementsByTagName()
        </p>
        <p>
          返回指定类名的元素的所有子元素集合：element.getElementsByClassName()
        </p>
        <p>返回元素是否含有某个属性：element.hasAttribute()</p>
        <p>返回元素是否含有任何属性：element.hasAttributes()</p>
        <p>返回文档或元素是否获取焦点：element.hasfocus()</p>
        <p>返回一个元素是否含有任何子元素：element.hasChildNodes()</p>
        <p>返回两个元素是否相等：element.isEqualNode(node)</p>
        <p>返回两个元素是否是同一个节点：element.isSameNode(node)</p>
        <p>
          返回当前节点是否支持某个特性：element.issupported(feature，version)
        </p>
        <p>创建指定节点的拷贝：element.cloneNode()</p>
        <p>
          比较当前节点和指定节点的文档位置：element.compareDocumentPosition(node)
        </p>
        <p>
          在指定子元素前插入一个新的子元素：element.insertBefore(newnode，existingnode)
        </p>
        <p>合并元素内相邻的文本节点并删除空的文本节点：element.normalize()</p>
        <p>返回元素的父节点：element.parentNode</p>
        <p>返回元素中匹配指定CSS选择器的第一个元素：element.querySelector()</p>
        <p>删除子节点列表中的某个节点：element.removeChild()</p>
        <p>调换某个子元素：element.replaceChild()</p>
      </el-collapse-item>
      <el-collapse-item
        title="返回元素的类名，作为DOMTokenlist：element.classList对象"
        name="3"
      >
        <p>在元素中添加一个或多个类名：element.classList.add(class1……)</p>
        <p>判断指定类名是否存在：element.classList.contain(class)</p>
        <p>返回指定索引值的类名：element.classList.item(index)</p>
        <p>移除元素中一个或多个类名：element. classList.remove(class1……)</p>
        <p>在元素中切换类名：element.classList.toggle(class，true|false)</p>
      </el-collapse-item>
      <el-collapse-item title="Dom属性对象方法" name="4">
        <p>返回属性是否是ID类型：attr.isId</p>
        <p>返回属性名称：attr.name</p>
        <p>设置或返回属性值：attr.value</p>
        <p>返回属性值是否被设定：attr.specified</p>
        <p>返回属性节点列表中指定属性名的值：attr.getNamedItem(name)</p>
        <p>返回属性节点列表中指定索引号的节点：attr.item(index)</p>
        <p>删除指定的节点：attr.removeNamedItem(name)</p>
        <p>添加指定节点：attr.setNameditem(name)</p>
      </el-collapse-item>
      <el-collapse-item title="Dom事件属性-鼠标事件" name="5">
        <p>单击：onclick</p>
        <p>右击：oncontextmenu</p>
        <p>双击：ondblclick</p>
        <p>按下：onmousedown</p>
        <p>松开：onmouseup</p>
        <p>指针移动到元素上：onmouseenter</p>
        <p>指针移出元素：onmouseleave</p>
        <p>指针移动：onmousemove</p>
        <p>指针在元素上：onmouseover</p>
        <p>指针不在元素上：onmouseout</p>
      </el-collapse-item>
      <el-collapse-item title="Dom事件属性-键盘事件" name="6">
        <p>按下(返回键盘的代码)：onkeydown</p>
        <p>松开：onkeyup</p>
        <p>按下(返回ASCII字符)：onkeypress</p>
      </el-collapse-item>
      <el-collapse-item title="Dom事件属性-框架/对象事件" name="7">
        <p>即将离开页面：onbeforeunload</p>
        <p>离开页面：onunload</p>
        <p>加载文档或图像时发生错误：onerror</p>
        <p>URL的锚部分发生改变：onhashchange</p>
        <p>文档或图像完成加载：onload</p>
        <p>页面被浏览：onpageshow</p>
        <p>离开当前页面：onpagehide</p>
        <p>窗口或框架被调整大小：onresize</p>
        <p>滚动滚动条：onscroll</p>
      </el-collapse-item>
      <el-collapse-item title="Dom事件属性-表单事件" name="8">
        <p>元素即将失去焦点：onfocusout</p>
        <p>元素失去焦点：onblur</p>
        <p>元素即将获得焦点：onfocusin</p>
        <p>元素获得焦点：onfocus</p>
        <p>表单元素内容发生改变：onchange</p>
        <p>表单元素获取到输入：oninput</p>
        <p>重置：onreset</p>
        <p>向搜索域输入文本：onsearch</p>
        <p>选中文本框中的文本：onselect</p>
        <p>提交：onsubmit</p>
      </el-collapse-item>
      <el-collapse-item title="Dom事件属性-剪贴板事件" name="9">
        <p>拷贝元素内容：oncopy</p>
        <p>剪切元素内容：oncut</p>
        <p>粘贴元素内容：onpaste</p>
        <p>开始打印或打印框已经关闭：onafterprint</p>
        <p>即将打印：onbeforeprint</p>
      </el-collapse-item>
      <el-collapse-item title="Dom事件属性-拖动事件" name="10">
        <p>元素正被拖动：ondrag</p>
        <p>开始拖动：ondragstart</p>
        <p>拖动完成：ondragend</p>
        <p>拖动元素进入有效放置区域：ondragenter</p>
        <p>拖动元素离开有效放置区域：ondragleave</p>
        <p>拖动元素在有效放置区域上：ondragover</p>
      </el-collapse-item>
      <el-collapse-item title="Dom事件属性-多媒体事件" name="11">
        <p>开始寻找视频/音频：onloadstart</p>
        <p>视频/音频的时长发生变化：ondurationchange</p>
        <p>指定视频/音频的元数据加载：onloadedmetadata</p>
        <p>加载完成当前帧：onloadeddata</p>
        <p>下载指定的视频/音频：onprogress</p>
        <p>可以开始播放视频/音频：oncanplay</p>
        <p>可以无需停顿或缓冲开始播放视频/音频：oncanplaythrough</p>
        <p>视频/音频中止加载：onabort</p>
        <p>视频/音频加载发生错误：onerror</p>
        <p>视频/音频播放结束：onended</p>
        <p>视频/音频暂停：onpause</p>
        <p>视频/音频开始播放：onplay</p>
        <p>视频/音频暂停或在缓冲后重新开始播放：onplaying</p>
        <p>视频/音频播放速度发生变化：onratechange</p>
        <p>开始定位播放进度：onseeking</p>
        <p>重新定位播放进度完成：onseeked</p>
        <p>获取媒体数据，但媒体数据不可用：onstalled</p>
        <p>读取媒体数据中止：onsuspend</p>
        <p>当前播放进度发生改变：ontimeupdate</p>
        <p>音量发生改变：onvolumechange</p>
        <p>视频播放下一帧需要缓冲：onwaiting</p>
      </el-collapse-item>
      <el-collapse-item title="Dom事件属性-动画/过渡事件" name="12">
        <p>css动画开始播放：animationstart</p>
        <p>css动画结束播放：animationend</p>
        <p>css动画重复播放：animationiteration</p>
        <p>css过渡完成：transitionend</p>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>
<script>
export default {
  methods: {
    handleChange() {},
  }
};
</script>
<style lang="less" scoped>
</style>