<template>
  <div>
    <el-collapse @change="handleChange" accordion>
      <el-collapse-item title="模板字符串" name="1">
        <p>
          模板字符串（template
          string）是增强版的字符串，用反引号（`）标识。它可以当作普通字符串使用，也可以用来定义多行字符串，或者在字符串中嵌入变量
        </p>
        <p>
          如果使用模板字符串表示多行字符串，所有的空格和缩进都会被保留在输出之中
        </p>
        <p>模板字符串中嵌入变量，需要将变量名写在${}之中</p>
        <p>
          大括号内部可以放入任意的JavaScript表达式，可以进行运算，引用对象属性,以及调用函数
        </p>
        <p>如果大括号中的值不是字符串，将按照一般的规则转为字符串</p>
        <p>
          由于模板字符串的大括号内部，就是执行JavaScript代码，因此如果大括号内部是一个字符串，将会原样输出
        </p>
        <p>模板字符串甚至还能嵌套</p>
        <p>如果需要引用模板字符串本身，在需要时执行，可以写成函数</p>
      </el-collapse-item>
      <el-collapse-item title="标签模板" name="2">
        <p>
          标签模板其实不是模板，而是函数调用的一种特殊形式。“标签”指的就是函数，紧跟在后面的模板字符串就是它的参数
        </p>
        <p>
          如果模板字符里面有变量，就不是简单的调用了，而是会将模板字符串先处理成多个参数，再调用函数
        </p>
        <p>
          “标签模板”的一个重要应用，就是过滤 HTML 字符串，防止用户输入恶意内容
        </p>
        <p>标签模板的另一个应用，就是多语言转换（国际化处理）</p>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>
<script>
export default {
  methods: {
    handleChange() {},
  },
};
</script>
<style lang="less" scoped>
</style>