<template>
  <div>
    <el-collapse @change="handleChange" accordion>
      <el-collapse-item title="null和undefine的区别" name="1">
        <p>null表示没有对象，即此处不应该有值</p>
        <p>undefine表示声明了但未赋值，即此处应该有值，但没有定义</p>
      </el-collapse-item>
      <el-collapse-item title="JS如何进行继承" name="2">
        <p>原型链、构造函数、组合式、原型式、寄生式、组合寄生式</p>
      </el-collapse-item>
      <el-collapse-item title="new操作符做了哪些事" name="3">
        <p>
          (1)创建一个新对象;
          (2)将构造函数的作用域赋给新对象(因此this就指向了这个新对象);
          (3)执行构造函数中的代码（为这个新对象添加属性);
          (4)返回新对象.
        </p>
      </el-collapse-item>
      <el-collapse-item title="闭包" name="4">
        <p>
          闭包就是函数，但是它可以继承并访问它自身被被声明的那个作用域里的变量。
        </p>
        <p>闭包按变量传递，函数按值传递。</p>
        <p>优点：逻辑连续、方便调用上下文的局部变量，加强封装性。</p>
        <p>缺点：容易造成内存浪费。</p>
      </el-collapse-item>
      <el-collapse-item title="JS的垃圾回收机制" name="5">
        <p>标记清除、引用计数</p>
      </el-collapse-item>
      <el-collapse-item title="cookie的利弊以及和web storage的区别" name="6">
        <p>
          cookie优点：不需要任何服务器资源，数据简单，基于文档的轻量结构，可配置生命周期。
        </p>
        <p>
          cookie缺点：数量和长度都有限制，有安全风险，可能被用户禁用，有些状态不可能保存在客户端。
        </p>
        <p>
          区别：存储空间不同；能否与服务器交互；API接口设置不同；能否跨域（cookie不能跨域）。
        </p>
      </el-collapse-item>
      <el-collapse-item title="bind()、apply()和call()的异同" name="7">
        <p>同：改变当前作用域中的this指向</p>
        <p>
          异：bind()用于方法，其他的用于函数，apply()和call()第一个参数都是新的this指向，而且可以传更多参数，apply()只需要传入一个所需参数的数组
          ，call()则需要一个一个的传入。
        </p>
      </el-collapse-item>
      <el-collapse-item title="关于Ajax以及Ajax的请求过程" name="8">
        <p>
          Ajax是js、xml、css、dom等多种技术的组合应用，用来实现客户端的异步操作，在不刷新页面的情况下和服务器进行通信，支持更新部分页面而不重载页面，减少用户的等待时间。
        </p>
        <p>
          创建XMLHttpRequest对象、创建一个新的HTTP请求，并指定请求方式，URL以及验证信息、设置响应HTTP请求状态变化的函数、发送请求、获取异步调用返回的数据、实现页面刷新。
        </p>
      </el-collapse-item>
      <el-collapse-item title="JSON" name="9">
        <p>
          一种轻量级的数据交换格式，易于阅读和书写，采用完全独立于程序语言的文本格式，也使用了类C语言的习惯，使得JSON成为理想的数据交换语言。
        </p>
      </el-collapse-item>
      <el-collapse-item title="深浅拷贝" name="10">
        <p>1.递归直接复制引用对象的键值对</p>
        <p>2.使用JSON对象的stringify和parse方法</p>
      </el-collapse-item>
      <el-collapse-item title="怎么判断一个对象是否是空的" name="11">
        <p>1.使用JSON.stringify将对象转为JSON字符串，再和'{}'比较</p>
        <p>2.使用Object.getOwnPropertyNames(),判断返回的数组长度是否为0</p>
        <p>3.使用Object.keys()或Object.values()，判断返回的数组长度是否为0</p>
      </el-collapse-item>
      <el-collapse-item title="如何实现前端路由" name="12">
        <p>
          要实现前端路由，需要解决两个核心问题：1.如何改变URL却不引起页面刷新；2.如何检测URL变化了
        </p>
        <p>
          hash实现：hash是URL中hash以及后面的部分，改变URL中的hash部分不会引起页面刷新；通过hashchange()事件监听URL的变化，改变URL的方式只有这几种：通过浏览器前进后退改变URL、通过a标签改变URL、通过window.location改变URL，这几种情况改变URL都会触发hashchange事件
        </p>
        <p>
          history实现：history提供了pushState和replaceState两个方法，这两个方法改变URL的path部分不会引起页面刷新；history提供类似hashchange的popstate事件，通过浏览器前进后退改变URL时会触发popstate事件，但是通过pushState/replaceState或a标签改变URL不会触发popstate事件，
          我们可以通过拦截pushState/replaceState的调用和a标签的点击事件来检测URL变化。
        </p>
      </el-collapse-item>
      <el-collapse-item title="类的实现和继承原理" name="13">
        <p>
          类的实现：调用_classCallCheck方法判断当前函数调用前是否有new关键字；将class内部的变量和函数赋给this；执行constuctor内部的逻辑；return
          this。
        </p>
        <p>
          继承的实现：调用_inherits函数继承父类的proptype；用一个闭包保存父类引用，在闭包内部做子类构造逻辑；new检查；用当前this调用父类构造函数；将子类class内部的变量和函数赋给this；执行子类constuctor内部的逻辑
        </p>
      </el-collapse-item>
      <el-collapse-item title="函数节流和函数防抖" name="14">
        <p>
          函数节流的要点是：声明一个变量当标志位，记录当前代码是否在执行，如果空闲，则可以正常触发方法执行，如果代码在执行中，则取消这次执行，直接return。函数节流不是避免执行方法，而是调用方法后会进行一次判断，满足条件才能执行，通常会给真正的方法体设置定时器，让其在固定事件内只执行一次。
        </p>
        <p>
          函数防抖的要点是：延迟执行需要跑的代码，如果方法多次触发，则不断的清除定时器，重新开始计时，如果计时完毕，期间没有触发方法，则正常执行代码。
        </p>
      </el-collapse-item>
      <el-collapse-item title="对模块化的理解" name="15">
        <p>
          封装细节，提供使用接口，彼此之间互不影响，每个模块都实现某一特定的功能。好处是可以避免变量污染，命名冲突，提高代码复用性，更好维护和管理。
        </p>
      </el-collapse-item>
      <el-collapse-item title="哪些操作会引起内存泄漏" name="16">
        <p>
          意外的全局变量；闭包；没有清理的DOM元素引用；被遗忘的定时器或回调；子元素存在引用。
        </p>
      </el-collapse-item>
      <el-collapse-item title="如何看待webpack" name="17">
        <p>
          WebPack
          是一个模块打包工具，可以使用WebPack管理模块依赖，并编绎输出模块们所需的静态文件。它能够很好地管理、打包Web开发中所用到的HTML、Js、CSS以及各种静态文件，让开发过程更加高效。对于不同类型的资源，webpack有对应的模块加载器。webpack模块打包器会分析模块间的依赖关系，最后生成优化且合并后的静态资源。
        </p>
      </el-collapse-item>
      <el-collapse-item title="定义对象的方法" name="18">
        <p>直接创建；实例化构造函数；Object.create()</p>
      </el-collapse-item>
      <el-collapse-item title="对AMD、CMD和CommonJs的理解" name="19">
        <p>
          在 ES6 之前，社区制定了一些模块加载方案，最主要的有 CommonJS 和 AMD
          两种。前者用于服务器，后者用于浏览器。ES6
          在语言标准的层面上，实现了模块功能，而且实现得相当简单，完全可以取代
          CommonJS 和 AMD 规范，成为浏览器和服务器通用的模块解决方案
        </p>
        <p>
          AMD/CMD/CommonJs是JS模块化开发的标准，目前对应的实现是RequireJs/SeaJs/nodeJs
        </p>
        <p>CommonJs主要针对服务端，AMD/CMD主要针对浏览器端</p>
        <p>
          AMD是预加载，在并行加载js文件同时，还会解析执行该模块；CMD是懒加载，并行加载js文件，在需要的时候才会执行
        </p>
        <p>AMD优缺点：加载速度快，但是JS文件执行的顺序不可控</p>
        <p>CMD优缺点：JS文件执行顺序可控，但是由于是同步执行，执行时间会叠加</p>
      </el-collapse-item>
      <el-collapse-item title="web开发中的会话跟踪" name="20">
        <p>cookie；session；隐藏表单域；URL重写；IP地址</p>
      </el-collapse-item>
      <el-collapse-item title="关于eval" name="21">
        <p>功能：把字符串解析成js代码执行并返回结果</p>
        <p>作用域：会把当前eval的作用域赋值过去</p>
        <p>注意：应该避免使用eval，不安全，耗性能</p>
        <p>应用：可以把json字符串转为json对象</p>
      </el-collapse-item>
      <el-collapse-item title="Js延迟加载" name="22">
        <p>defer；async；动态创建脚本；定时器延迟执行方法；把脚本放到底部</p>
      </el-collapse-item>
      <el-collapse-item title="渐进增强和优雅降级" name="23">
        <p>
          渐进增强：针对低版本浏览器进行构建页面，保证最基本的功能，然后再针对高级浏览器进行效果、交互等改进和追加功能达到更好的用户体验
        </p>
        <p>优雅降级：一开始就构建完整的功能，然后再针对低版本浏览器进行兼容</p>
        <p>
          区别：优雅降级是从复杂的现状开始，并试图减少用户体验的供给，而渐进增强则是从一个非常基础的、能够起作用的版本开始，并不断扩充，以适应未来环境的需要
        </p>
      </el-collapse-item>
      <el-collapse-item title="attribute和property的区别" name="24">
        <p>
          property是DOM中的属性，是JavaScript里的对象；attribute是HTML标签上的特性，它的值只能是字符串。某种意义上property包含了attribute。
        </p>
      </el-collapse-item>
      <el-collapse-item title="为什么操作DOM慢" name="25">
        <p>
          DOM本身是一个js对象, 操作这个对象本身不慢,
          但是操作后触发了浏览器的行为, 如repaint和reflow等浏览器行为, 使其变慢
        </p>
      </el-collapse-item>
      <el-collapse-item title="CSRF和XSS区别和预防" name="26">
        <p>
          CSRF（Cross-site request
          forgery）跨站请求伪造：假设你登陆了a网站，此时你又打开了b网站的某个页面，b网站的某个页面上有一段代码，可能是一个自提交的表单，表单的action是a网站，这样b网站就模拟了你的身份，向a网站发送了一个请求。CSRF的攻击特点是伪造其他网站的操作，冒用身份比如利用cookie伪造登录凭证，不是窃取cookie。
        </p>
        <p>
          解决方案：对于跨站请求伪造，最重要的就是区分访问源头。因为发生在其他人的网站，防止攻击来源的产生是不可能的，只能从提升自己网站的甄别度来解决。1.有每一个表单都带有一个CSRF令牌（CSRF
          TOKEN）,后端验证没有令牌的直接被拒绝，这里的令牌需要和会话绑定更能确保安全性，尤其是不要提供获取CSRF令牌的接口，否则就形同虚设了；2.验证HTTP
          Referer字段；3.验证码（现在基本大型网站重要的操作都会进行验证，甚至是手机验证码验证来保障安全）
        </p>
        <p>
          XSS （Cross-site
          scripting）跨站脚本攻击，这里缩写css被前端层叠样式表（Cascading Style
          Sheets）占用了，为了区分就叫了xss。属于注入攻击的一种。窃取用户cookie，损害/控制用户电脑做出某些行为，模拟用户操作等。xss的攻击如果成功，一般来说是可以造成很严重后果的，因为对方相当于直接控制了你的操作，并做出一些你自己不会做的事情，CSRF相对来说，只是模拟了操作，还是受到服务提供者限制的。
        </p>
        <p>
          解决方案：网站不应该信任用户输入的内容，应对用户输入内容进行处理，过滤任何有执行能力的脚本或者影响页面的CSS，保证其他用户访问页面的安全。具体针对每种语言都有相应的处理机制和工具。一般各种后端框架都封装或者提供相关插件处理。
        </p>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>
<script>
export default {
  methods: {
    handleChange() {},
  },
};
</script>
<style lang="less" scoped>
