<template>
  <div>
    <el-collapse @change="handleChange" accordion>
      <el-collapse-item title="css实现自动水平垂直居中" name="1">
        <p>
          1.父元素display：flex;然后align-items：center;justify-contetn：center;
        </p>
        <p>
          2.position：absolute;分两种：1.left：50%;top：50%;之后用margin-left或者transform要居中元素宽高一半的负值；2.left：0;right：0;top：0;bottom：0;形成自适应流元素，设定宽高之后margin：auto;
        </p>
        <p>
          3.父元素display：table;子元素display：table-cell;text-align：center;verti-align：middle;孙子元素display：inline-block;
        </p>
      </el-collapse-item>
      <el-collapse-item title="meta标签" name="2">
        <p>
          提供了HTML的元数据，不会显示在客户端，但是会被浏览器解析。meta元素通常用于指定网页的描述，关键词，文件的最后修改时间，作者以及其他元数据。
        </p>
      </el-collapse-item>
      <el-collapse-item
        title="display：none、visibility：hidden和opacity：0的区别"
        name="3"
      >
        <p>
          visibility和opacity隐藏后依然占据空间，会被子元素继承，opacity能被transition过渡识别，其他的不行。
        </p>
        <p>
          display：none
          让元素隐藏，隐藏后不占空间，不会被子元素继承，不会触发事件，transition无效
        </p>
        <p>
          visibility：hidden
          让元素隐藏，隐藏后依然占据空间，会被子元素继承，不会触发事件，transition无效
        </p>
        <p>
          opacity：0
          让元素隐藏，隐藏后依然占据空间，会被子元素继承，可以触发事件，transition有效
        </p>
      </el-collapse-item>
      <el-collapse-item title="语义化的理解" name="4">
        <p>
          1.HTML5的语义化指的是正确的使用语义化的标签来创建页面结构，所谓语义化的标签即从标签的名字可以直观的知道这个标签的作用。
        </p>
        <p>
          2.代码结构清晰，易于阅读，利于开发和维护；方便其他设备解析，根据语义渲染网页；有利于搜索引擎优化。
        </p>
      </el-collapse-item>
      <el-collapse-item
        title="Doctype作用，严格模式和混杂模式如何区分以及有何意义"
        name="5"
      >
        <p>
          DOCTYPE位于文档中的第一行，处于html标签之前，就告知浏览器的解析器用什么文档标准进行解析。严格模式（标准模式）的排版和JS运作模式都以浏览器支持的最高标准运行；混杂模式（兼容模式或怪异模式）里页面以更宽松的向后兼容的方式显示，以防站点无法工作，当DOCTYPE不存在或格式不正确时文档会以混杂模式呈现。
        </p>
      </el-collapse-item>
      <el-collapse-item title="如何清除浮动" name="6">
        <p>1.添加额外标签，clear：both;</p>
        <p>2.父元素overflow：hidden;</p>
        <p>3.使用before和after伪元素，clear：both</p>
        <p>4.给父元素设置高度</p>
        <p>5.让父元素也浮动</p>
      </el-collapse-item>
      <el-collapse-item title="HTML5新特性" name="7">
        <p>
          语义化标签；增强型表单；视频与音频；canvas绘图；svg绘图；地理定位；拖放API；Web
          Worker；Web Storage；Websocket
        </p>
      </el-collapse-item>
      <el-collapse-item title="BFC" name="8">
        <p>
          W3C对BFC的定义：浮动元素和绝对定位元素，非块级盒子的块级容器，以及overflow不为visible。
        </p>
      </el-collapse-item>
      <el-collapse-item title="Canvas和SVG有什么区别" name="9">
        <p>
          svg缩放不影响显示,svg对象的属性发生变化，那么浏览器能够自动重现图形。canvas中，一旦图形被绘制完成，它就不会继续得到浏览器的关注。如果其位置发生变化，那么整个场景也需要重新绘制，包括任何或许已被图形覆盖的对象
        </p>
        <p>
          svg不依赖分辨率，支持事件处理器，最适合带有大型渲染区域的应用程序（比如谷歌地图），复杂度高会减慢渲染速度（任何过度使用DOM的应用都不快）
        </p>
        <p>
          canvas依赖分辨率，不支持事件处理器，弱的文本渲染能力，能够以.png或.jpg格式保存结果图像，最适合图像密集型的游戏，其中的许多对象会被频繁重绘
        </p>
      </el-collapse-item>
      <el-collapse-item title="FOUC" name="10">
        <p>FOUC：文档样式短暂失效(Flash of Unstyled Content)</p>
        <p>
          造成原因：使用import导入样式表，当样式表晚于结构性html加载，当加载到此样式表时，页面将停止之前的渲染。当样式表被下载和解析后，将重新渲染页面，也就出现了短暂的花屏现象。
        </p>
        <p>解决办法：使用link标签引入样式表</p>
      </el-collapse-item>
      <el-collapse-item title="为什么要初始化css样式" name="11">
        <p>
          不同的浏览器对有些标签的默认值是不同的，初始化CSS能避免因为浏览器的不同使得页面显示差异，而且也有助于之后页面排版和书写
        </p>
      </el-collapse-item>
      <el-collapse-item title="媒体查询" name="12">
        <p>
          使用 @media 查询，你可以针对不同的媒体类型定义不同的样式。@media
          可以针对不同的屏幕尺寸设置不同的样式，特别是如果你需要设置设计响应式的页面，@media
          是非常有用的。当你重置浏览器大小的过程中，页面也会根据浏览器的宽度和高度重新渲染页面。
        </p>
        <p>
          css语法： @media mediatype and|not|only (media feature) { CSS-Code; }
          或者你也可以针对不同的媒体使用不同stylesheets： &lt;link
          rel="stylesheet" media="mediatype and|not|only (media feature)"
          href="mystylesheet.css"&gt;
        </p>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>
<script>
export default {
  methods: {
    handleChange() {},
  }
};
</script>
<style lang="less" scoped>
</style>